
.service-card{
  background: #f8f8ff;
  @include transition;

  &:hover{
    background: $white;
  }
}
.service-card-img{
  padding-top: 60px;
  padding-bottom: 40px;
  max-height: 260px;

  @media(max-width: 600px){
    padding-top: 20px;
    padding: 10px;
  }
}
.service-card-body{
  padding: 10px 30px 40px 30px;

  h3{
    margin-bottom: 8px;

    @media(min-width: 768px){
      margin-bottom: 15px;
    }
  }
}




.about-content{

  h2{
    margin-bottom: 15px;

    @media(min-width: 768px){
      margin-bottom: 25px;
    }
  }

  p{
    margin-bottom: 20px;

    @media(min-width: 768px){
      margin-bottom: 40px;
    }
  }
}
.about-img{

  @media(min-width: 1200px){
    position: relative;
    left: -100px;
  }
}







