/* footer style start
	============================================================================================ */

.footer-area {
  background: #394359;
  color: #fff;
  font-size: 12px;
  height: $max-footer-height;
  max-height: $max-footer-height;
  padding-top: 30px;

  #hr {
    margin: 0 15%;
    background-color: rgba(255, 255, 255, 0.3);
  }

  .social-icons{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }


  @media (min-width: 1000px) {
    // padding-top: 110px;
  }
  .footer-nav {
    li {
      margin-top: 18px;
      a {
        color: #fff;
        &:hover {
          color: $primary-color;
        }
      }
    }
  }

  .primary-btn {
    background-color: $primary-color;
    line-height: 42px;
    padding-left: 30px;
    padding-right: 60px;
    border-radius: 25px;
    border: none;
    color: $white;
    display: inline-block;
    font-weight: 500;
    position: relative;
    @include transition();
    cursor: pointer;
    text-transform: uppercase;
    position: relative;
    &:focus {
      outline: none;
    }
    span {
      color: $white;
      position: absolute;
      top: 50%;
      transform: translateY(-60%);
      right: 30px;
      @include transition();
    }
    &:hover {
      background-color: $primary-color;
      box-shadow: none;
      color: $white;
      span {
        color: $white;
        right: 20px;
      }
    }
    &.white {
      border: 1px solid $white;
      color: $white;
      span {
        color: $white;
      }
      &:hover {
        background: $white;
        color: $primary-color;
        span {
          color: $primary-color;
        }
      }
    }
  }
  h6 {
    text-transform: uppercase;
    color: #D88A83;
    margin-bottom: 25px;
    font-size: 20px;
    font-weight: 700;
  }
}
.copy-right-text {
  background-color: rgba(0, 0, 0, 0.2);
  overflow: hidden;
  color: rgba(255, 255, 255, 0.6);
  i,
  a {
    color: $primary-color;
  }
}

.instafeed {
  margin: -5px;
  li {
    overflow: hidden;
    width: 25%;
    margin-bottom: 0 !important;
    img {
      margin: 5px;
    }
  }
}

.footer-social {
  text-align: right;
  a {
    width: 20px;
    width: 40px;
    display: inline-table;
    height: 40px;
    text-align: center;
    padding-top: 10px;
    @include transition();
    &:hover {
      i {
        color: $primary-color;
      }
    }
  }

  i,
  span {
    font-size: 14px;
    color: rgba(221, 221, 221, 0.5);
    @include transition();
  }
  @media (max-width: 991.98px) {
    text-align: left;
    margin-top: 20px;
  }
}

.single-footer-widget {
  color: #fff;
  input {
    line-height: 38px;
    border: none;
    background: transparent;
    border: 1px solid #333333;
    font-weight: 400;
    color: #999999;
    padding-left: 20px;
    width: 80%;
    font-size: 14px;

    &:focus {
      outline: 0;
      box-shadow: none;
    }

    &::placeholder {
      font-weight: 400;
    }
  }
  .bb-btn {
    background-color: $primary-color;
    color: #fff;
    font-size: 13px;
    font-weight: 300;
    border-radius: 0;
    cursor: pointer;
  }

  @media (max-width: 960px) {
    .nw-btn {
      margin-top: 20px;
    }
    .newsletter {
      padding-bottom: 40px;
    }
  }

  .info {
    position: absolute;
    margin-top: 3px;
    color: #fff;
    font-size: 14px;
    &.valid {
      color: green;
    }
    &.error {
      color: $primary-color;
    }
  }

  ::-moz-selection {
    /* Code for Firefox */
    background-color: $primary-color;
    color: $text-color;
  }
  ::selection {
    background-color: $primary-color;
    color: $text-color;
  }
  ::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: $text-color;
    font-weight: 300;
  }
  :-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: $text-color;
    opacity: 1;
    font-weight: 300;
  }
  ::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: $text-color;
    opacity: 1;
    font-weight: 300;
  }
  :-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: $text-color;
    font-weight: 300;
  }
  ::-ms-input-placeholder {
    /* Microsoft Edge */
    color: $text-color;
    font-weight: 300;
  }
  @media (max-width: 991.98px) {
    margin-bottom: 45px;
  }
  p {
    color: $link-color;
  }
  ul {
    li {
      margin-bottom: 10px;
      a {
        color: $link-color;
        @include transition();
        &:hover {
          color: $primary-color;
        }
      }
    }
  }
}

.footer-bottom {
  padding: 15px 0;
  margin-top: 30px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);

  @media (min-width: 1000px) {
    margin-top: 70px;
  }
}

.footer-text {
  a,
  i {
    color: $primary-color;
  }
}

/* footer style end
	============================================================================================ */
